import React from 'react';

import './Loading.css';

const Loading = ( props ) => {

    return (
        <div className="Loading" style={ { height: props.loadingHeight ? props.loadingHeight : '100' + props.unit } }>
            <div>
                <img src={ props.image } width={ props.width } height={ props.height } alt="loading..." />
                <p style={ { color: '#000', marginTop: '8px', textAlign: 'center' } }><small>{ props.txt }</small></p>
            </div>
        </div>
    )

}

export default Loading;