import React, { lazy, Suspense } from "react";

import "./App.css";

import { Switch, Route } from "react-router-dom";
import Loading from "./components/Loading/Loading";
import LoadingImg from './assets/images/loading2.gif';

const Login = lazy( () => import("./components/Login/Login") );
const StudentList = lazy( () => import("./components/StudentList/StudentList") );
const StudentEntry = lazy( () => import("./components/StudentEntry/StudentEntry") );

const App = () => {

  return (
    
      <Suspense fallback={ <Loading image={ LoadingImg } width="50" height="50" txt="Loading..." unit="vh" /> }>
        <Switch>
          <Route exact path="/" component={ Login } />
          <Route exact path="/students/list" component={ StudentList } />
          <Route exact path="/students/entry=new" component={ StudentEntry } />
          <Route exact path="/students/entry=edit&&registration=:id" component={ StudentEntry } />
        </Switch>
      </Suspense>

  )
}

export default App;
